import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "../redux/data/dataActions";
import { connect } from "../redux/blockchain/blockchainActions";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import Alert from "@mui/material/Alert";
import FilledInput from "@mui/material/FilledInput";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import RemoveOutlinedIcon from "@mui/icons-material/RemoveOutlined";
import AddOutlinedIcon from "@mui/icons-material/AddOutlined";
import CircleIcon from "@mui/icons-material/Circle";
import LoadingButton from "@mui/lab/LoadingButton";
import CircularProgress from "@mui/material/CircularProgress";
import Countdown from "react-countdown";
import Chip from "@mui/material/Chip";
import Link from "@mui/material/Link";
import { isMobile } from "react-device-detect";

import CONFIG from "../config.json";

const shorten = (str: string): string => {
  if (str.length < 10) return str;
  return `${str.slice(0, 6)}...${str.slice(str.length - 4)}`;
};

enum MINT_STATE {
  NOT_CONNECTED,
  WRONG_NETWORK,
  CONNECTED,
  NOT_STARTED,
  WHITELISTED_ONLY,
  PUBLIC_SALE,
  PRE_SALE,
  CLAIMING,
  MINTED,
  ERROR,
  CLOSED,
  LOADING,
}

function Actions() {
  const dispatch = useDispatch();
  const data = useSelector<any, any>((state) => state.data);
  const blockchain = useSelector<any, any>((state) => state.blockchain);
  const [claimingNft, setClaimingNft] = useState(false);
  const [mintAmount, setMintAmount] = useState(1);
  const [error, setError] = useState("");
  const [mintReceipt, setMintReceipt] = useState(null);
  const getData = () => {
    if (blockchain.account !== "" && blockchain.minter !== null) {
      dispatch(fetchData());
    }
  };

  useEffect(
    () => setMintAmount(data.maxPerWallet - data.amountMinted),
    [data.maxPerWallet, data.amountMinted]
  );

  useEffect(() => getData(), [blockchain.account]);

  function catchError(err: Error) {
    console.error(err);
    if (err.message) {
      setError(err.message);
    } else {
      setError("Something went wrong while trying to mint the NFT");
    }
  }

  const claimNFTs = async () => {
    setClaimingNft(true);
    try {
      const cost = data.whitelistedOnly
        ? CONFIG.WL_WEI_COST
        : CONFIG.PUBLIC_WEI_COST;
      const receipt = await blockchain.minter.methods.mint(mintAmount).send({
        to: CONFIG.MINTER_ADDRESS,
        from: blockchain.account,
        value: cost * mintAmount,
      });
      console.log(receipt);
      setClaimingNft(false);
      setMintReceipt(receipt);
      dispatch(fetchData());
    } catch (err) {
      catchError(err);
      setClaimingNft(false);
    }
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (
      newMintAmount <=
      Math.min(
        data.maxPerWallet - data.amountMinted,
        data.availableSupply - data.totalSupply
      )
    ) {
      setMintAmount(newMintAmount);
    }
  };

  function getMintState(): MINT_STATE {
    if (!blockchain.account || blockchain.minter === null) {
      return MINT_STATE.NOT_CONNECTED;
    } else if (blockchain.networkId != CONFIG.NETWORK.ID) {
      return MINT_STATE.WRONG_NETWORK;
    } else if (data.mintPaused) {
      return MINT_STATE.NOT_STARTED;
    } else if (claimingNft) {
      return MINT_STATE.CLAIMING;
    } else if (mintReceipt) {
      return MINT_STATE.MINTED;
    } else if (data.totalSupply >= data.availableSupply || data.mintEnded) {
      return MINT_STATE.CLOSED;
    } else if (data.whitelistedOnly) {
      return data.isWhitelisted
        ? MINT_STATE.PUBLIC_SALE
        : MINT_STATE.WHITELISTED_ONLY;
    } else if (data.mintPaused === false) {
      return MINT_STATE.PUBLIC_SALE;
    }
    return MINT_STATE.LOADING;
  }

  if (getMintState() === MINT_STATE.NOT_CONNECTED) {
    return (
      <Stack spacing={2}>
        {isMobile ? (
          <>
            {typeof window.ethereum === "undefined" ? (
              <>
                <Typography>Continue in</Typography>
                <Stack direction="row" spacing={2}>
                  <Button
                    variant="contained"
                    fullWidth
                    href="https://metamask.app.link/dapp/t.co/PdqQplEb1N"
                  >
                    Metamask
                  </Button>
                  <Button
                    onClick={(e) => {
                      e.preventDefault();
                      dispatch(connect());
                      getData();
                    }}
                    variant="contained"
                    fullWidth
                  >
                    Coinbase
                  </Button>
                </Stack>
              </>
            ) : (
              <Button
                onClick={(e) => {
                  e.preventDefault();
                  dispatch(connect());
                  getData();
                }}
                variant="contained"
                fullWidth
              >
                Connect Your Wallet
              </Button>
            )}
          </>
        ) : (
          <Button
            onClick={(e) => {
              e.preventDefault();
              dispatch(connect());
              getData();
            }}
            variant="contained"
            fullWidth
          >
            Connect Your Wallet
          </Button>
        )}
      </Stack>
    );
  } else if (getMintState() === MINT_STATE.WRONG_NETWORK) {
    return (
      <Alert variant="outlined" severity="error">
        You are not connected to the {CONFIG.NETWORK.NAME} network, please open
        Metamask and make sure you are connected with the right account to the
        Ethereum network.
      </Alert>
    );
  } else if (getMintState() === MINT_STATE.NOT_STARTED) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        <Typography variant="body1" gutterBottom align="center">
          Minting has not started yet. Once the timer reaches zero, refresh this
          page and be the first to mint.
        </Typography>
      </Stack>
    );
  } else if (
    getMintState() === MINT_STATE.PUBLIC_SALE ||
    getMintState() === MINT_STATE.CLAIMING
  ) {
    return (
      <>
        <Stack direction="row" spacing={4}>
          <FilledInput
            value={mintAmount}
            disableUnderline
            startAdornment={
              <InputAdornment position="start">
                <IconButton
                  aria-label="decrement"
                  size="small"
                  onClick={(e) => {
                    e.preventDefault();
                    decrementMintAmount();
                  }}
                  disabled={getMintState() === MINT_STATE.CLAIMING}
                >
                  <RemoveOutlinedIcon />
                </IconButton>
              </InputAdornment>
            }
            endAdornment={
              <InputAdornment position="start">
                <IconButton
                  aria-label="decrement"
                  size="small"
                  onClick={(e) => {
                    e.preventDefault();
                    incrementMintAmount();
                  }}
                  disabled={getMintState() === MINT_STATE.CLAIMING}
                >
                  <AddOutlinedIcon />
                </IconButton>
              </InputAdornment>
            }
            inputProps={{
              style: {
                textAlign: "center",
              },
            }}
            fullWidth
          />
          <LoadingButton
            disabled={getMintState() === MINT_STATE.CLAIMING}
            loading={getMintState() === MINT_STATE.CLAIMING}
            variant="contained"
            onClick={(e) => {
              e.preventDefault();
              claimNFTs();
              getData();
            }}
            fullWidth
          >
            Mint NFT
          </LoadingButton>
        </Stack>
        <Typography variant="subtitle2">
          {data.amountMinted} of {data.maxPerWallet} minted
        </Typography>
        {error && (
          <Alert variant="outlined" severity="error">
            {error}
          </Alert>
        )}
      </>
    );
  } else if (getMintState() === MINT_STATE.CLOSED) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={2}
      >
        <Typography variant="body1" gutterBottom align="center">
          Minting period has ended. The whole collection be will revealed soon,
          go to OpenSea to check it out!
        </Typography>
        <Button
          variant="contained"
          href={CONFIG.MARKETPLACE_LINK}
          target="_blank"
        >
          See collection
        </Button>
      </Stack>
    );
  } else if (getMintState() === MINT_STATE.MINTED) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        {/* <Typography variant="h2">NFT has been minted</Typography> */}
        <Typography variant="body1" gutterBottom align="center">
          NFT has been minted! It should soon be visible in your OpenSea
          account, you can check the transaction below to follow it's progress.
        </Typography>
        <Button
          variant="contained"
          href={`https://etherscan.io/tx/${mintReceipt.transactionHash}`}
          target="_blank"
        >
          View Transaction
        </Button>
      </Stack>
    );
  } else if (getMintState() === MINT_STATE.WHITELISTED_ONLY) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        <Typography variant="body1" gutterBottom align="center">
          The wallet you are using is not whitelisted, keep an eye on this page
          to be the first when the public sale starts.
        </Typography>
      </Stack>
    );
  }
  return (
    <Stack direction="column" justifyContent="center" alignItems="center">
      <CircularProgress size={24} />
    </Stack>
  );
}

export default function Mint() {
  const dispatch = useDispatch();
  const cost = CONFIG.PUBLIC_WEI_COST;
  const blockchain = useSelector<any, any>((state) => state.blockchain);
  useEffect(() => {
    if (!("Notification" in window)) {
      console.log("This browser does not support desktop notification");
    } else {
      Notification.requestPermission();
    }
  }, []);
  const data = useSelector<any, any>((state) => state.data);

  return (
    <Stack spacing={2} justifyContent="space-between" flex={1}>
      <Stack mb={1}>
        <Box>
          {blockchain.account && (
            <Chip
              size="small"
              label={`Connected with ${shorten(blockchain.account)}`}
              icon={
                <CircleIcon
                  sx={{
                    fontSize: "12px!important",
                    marginLeft: "12px!important",
                    color: "lightgreen!important",
                  }}
                />
              }
              sx={{
                fontSize: "12px!important",
              }}
            />
          )}
        </Box>
      </Stack>
      <Stack direction="row" justifyContent="space-between">
        <Box>
          <Typography variant="subtitle2" align="left" gutterBottom>
            Price
          </Typography>
          <Typography variant="subtitle1" align="left">
            {cost / 1e18} {CONFIG.NETWORK.SYMBOL}
          </Typography>
        </Box>
        <Box>
          <Typography variant="subtitle2" align="right" gutterBottom>
            1st Wave
          </Typography>
          <Typography variant="subtitle1" align="right" gutterBottom>
            {data.totalSupply}/737
          </Typography>
        </Box>
      </Stack>
      <Actions />
    </Stack>
  );
  return null;
}
