const initialState = {
  loading: false,
  account: null,
  minter: null,
  mintPass: null,
  erc721: null,
  web3: null,
  errorMsg: "",
  networkId: null,
  hasParticipatedInRaffle: null,
};

const blockchainReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CONNECTION_REQUEST":
      return {
        ...initialState,
        loading: true,
      };
    case "CONNECTION_SUCCESS":
      return {
        ...state,
        loading: false,
        account: action.payload.account,
        minter: action.payload.minter,
        mintPass: action.payload.mintPass,
        erc721: action.payload.erc721,
        web3: action.payload.web3,
        networkId: action.payload.networkId,
      };
    case "CONNECTION_FAILED":
      return {
        ...initialState,
        loading: false,
        errorMsg: action.payload,
      };
    case "UPDATE_ACCOUNT":
      return {
        ...state,
        account: action.payload.account,
      };
    case "RAFFLE_STATUS":
      return {
        ...state,
        hasParticipatedInRaffle: action.payload.hasParticipatedInRaffle,
      };
    default:
      return state;
  }
};

export default blockchainReducer;
