import store from "../store";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchData = () => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let totalSupply = await store
        .getState()
        .blockchain.erc721.methods.totalSupply()
        .call();

      let availableSupply = await store
        .getState()
        .blockchain.minter.methods.availableSupply()
        .call();

      // let price = await store
      //   .getState()
      //   .blockchain.minter.methods.PRICE()
      //   .call();

      let maxPerWallet = await store
        .getState()
        .blockchain.minter.methods.MAX_PER_WALLET()
        .call();

      let mintPaused = await store
        .getState()
        .blockchain.minter.methods.mintPaused()
        .call();

      let mintEnded = await store
        .getState()
        .blockchain.minter.methods.mintEnded()
        .call();

      let whitelistedOnly = await store
        .getState()
        .blockchain.minter.methods.whitelistedOnly()
        .call();

      let account = await store.getState().blockchain.account;

      let isWhitelisted = await store
        .getState()
        .blockchain.minter.methods.whitelisted(account)
        .call();

      let amountMinted = await store
        .getState()
        .blockchain.minter.methods.mintedSale(account)
        .call();

      dispatch(
        fetchDataSuccess({
          totalSupply: +totalSupply,
          availableSupply: +availableSupply,
          // price: price,
          maxPerWallet: +maxPerWallet,
          mintPaused,
          mintEnded,
          whitelistedOnly,
          isWhitelisted,
          amountMinted: +amountMinted,
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};
