import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { sign } from "../redux/blockchain/blockchainActions";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import LoadingButton from "@mui/lab/LoadingButton";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";
import CircularProgress from "@mui/material/CircularProgress";

enum RAFFLE_STATE {
  NOT_CONNECTED,
  NOT_STARTED,
  OPEN,
  ALREADY_PARTICIPATED,
  CLOSED,
}

export default function Raffle() {
  const dispatch = useDispatch();
  const [raffleState, setRaffleState] = useState<RAFFLE_STATE>(
    RAFFLE_STATE.OPEN
  );
  const blockchain = useSelector<any, any>((state) => state.blockchain);
  useEffect(() => getData(), [blockchain.account]);
  const getData = () => {
    if (blockchain.account !== "" && blockchain.minter !== null) {
      dispatch(fetchData());
    }
  };
  if (raffleState === RAFFLE_STATE.NOT_STARTED) {
    return (
      <Stack spacing={2} flex={1}>
        <Typography variant="h2">Raffle will soon start</Typography>
        <Typography variant="body1" gutterBottom align="center">
          Come back later to participate in the raffle to get a chance to get
          whitelisted.
        </Typography>
      </Stack>
    );
  } else if (!blockchain.account && raffleState === RAFFLE_STATE.OPEN) {
    return (
      <Stack spacing={2} flex={1}>
        <Typography variant="h2">Raffle is now open</Typography>
        <Typography variant="body1" gutterBottom align="center">
          Connect with your wallet to join the raffle, and get a chance to get
          whitelisted.
        </Typography>
        <Button
          onClick={(e) => {
            e.preventDefault();
            dispatch(connect());
            getData();
          }}
          variant="contained"
          fullWidth
        >
          Connect Your Wallet
        </Button>
      </Stack>
    );
  } else if (
    raffleState === RAFFLE_STATE.OPEN &&
    blockchain.hasParticipatedInRaffle === false
  ) {
    return (
      <Stack spacing={2} flex={1}>
        <Typography variant="h2">Take now part in raffle</Typography>
        <Typography variant="body1" gutterBottom align="center">
          Simply click the button below to take part in the raffle without
          paying any gas.
        </Typography>
        <LoadingButton
          variant="contained"
          onClick={(e) => {
            dispatch(sign());
          }}
          fullWidth
        >
          Join Raffle
        </LoadingButton>
      </Stack>
    );
  } else if (
    (raffleState === RAFFLE_STATE.OPEN ||
      raffleState === RAFFLE_STATE.CLOSED) &&
    blockchain.hasParticipatedInRaffle === true
  ) {
    return (
      <Stack spacing={2} flex={1}>
        <Typography variant="h2">
          You claimed your spot in the raffle
        </Typography>
        <Typography variant="body1" gutterBottom align="center">
          Make sure to follow us to get notified when the results are in and the
          mint starts.
        </Typography>
      </Stack>
    );
  } else if (raffleState === RAFFLE_STATE.CLOSED) {
    return (
      <Stack spacing={2} flex={1}>
        <Typography variant="h2">The raffle has been closed</Typography>
        <Typography variant="body1" gutterBottom align="center"></Typography>
      </Stack>
    );
  }
  return (
    <Stack spacing={2} justifyContent="space-between" flex={1}>
      <Stack direction="column" justifyContent="center" alignItems="center">
        <CircularProgress size={24} />
      </Stack>
    </Stack>
  );
}
