import React, { useState } from "react";
import Raffle from "./states/Raffle";
import Mint from "./states/Mint";
import CONFIG from "./config.json";

function App() {
  if (CONFIG.PRIMARY_STATE === "RAFFLE") {
    return <Raffle />;
  } else if (CONFIG.PRIMARY_STATE === "MINT") {
    return <Mint />;
  }
}

export default App;
