import CONFIG from "../../config.json";

const initialState = {
  loading: false,
  totalSupply: "?",
  availableSupply: CONFIG.MAX_SUPPLY,
  price: CONFIG.PUBLIC_WEI_COST / 1e18,
  maxPerWallet: null,
  mintPaused: null,
  mintEnded: null,
  whitelistedOnly: null,
  isWhitelisted: null,
  amountMinted: null,
  error: false,
  errorMsg: "",
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHECK_DATA_REQUEST":
      return {
        ...state,
        loading: true,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_SUCCESS":
      return {
        ...state,
        loading: false,
        totalSupply: action.payload.totalSupply,
        availableSupply: action.payload.availableSupply,
        price: action.payload.price,
        maxPerWallet: action.payload.maxPerWallet,
        mintPaused: action.payload.mintPaused,
        mintEnded: action.payload.mintEnded,
        whitelistedOnly: action.payload.whitelistedOnly,
        isWhitelisted: action.payload.isWhitelisted,
        amountMinted: action.payload.amountMinted,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_FAILED":
      return {
        ...initialState,
        loading: false,
        error: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
};

export default dataReducer;
