import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";
import Web3Modal from "web3modal";
import WalletConnect from "@walletconnect/web3-provider";
import WalletLink from "walletlink";
import { fetchData } from "../data/dataActions";
import minterABI from "../../abi/minter.json";
import mintPassABI from "../../abi/mintpass.json";
import erc721ABI from "../../abi/erc721.json";
import CONFIG from "../../config.json";

const connectRequest = () => {
  return {
    type: "CONNECTION_REQUEST",
  };
};

const connectSuccess = (payload) => {
  return {
    type: "CONNECTION_SUCCESS",
    payload: payload,
  };
};

const connectFailed = (payload) => {
  return {
    type: "CONNECTION_FAILED",
    payload: payload,
  };
};

const updateAccountRequest = (payload) => {
  return {
    type: "UPDATE_ACCOUNT",
    payload: payload,
  };
};

const receiveNetworkId = (payload) => {
  return {
    type: "RECEIVE_NETWORK",
    payload: payload,
  };
};

const updateRaffleStatus = (payload) => {
  return {
    type: "RAFFLE_STATUS",
    payload: payload,
  };
};

const providerOptions = {
  // walletconnect: {
  //   package: WalletConnect,
  //   options: {
  //     infuraId: "1cdd280a1c68480f908bf48406db4ca5",
  //   },
  // },
  walletlink: {
    package: WalletLink,
    options: {
      appName: "MLK Mint",
      infuraId: "1cdd280a1c68480f908bf48406db4ca5",
    },
  },
};

const web3Modal = new Web3Modal({
  // network: "mainnet", // optional
  cacheProvider: false, // optional
  providerOptions, // required
  // disableInjectedProvider: true,
});

export const connect = () => {
  return async (dispatch) => {
    await web3Modal.clearCachedProvider();
    const provider = await web3Modal.connect();
    dispatch(connectRequest());
    // Web3EthContract.setProvider(provider);
    let web3 = new Web3(provider);
    try {
      const accounts = await web3.eth.getAccounts();
      const networkId = await web3.eth.net.getId();
      const minter = new web3.eth.Contract(
        // @ts-expect-error
        minterABI,
        CONFIG.MINTER_ADDRESS
      );
      const mintPass = new web3.eth.Contract(
        // @ts-expect-error
        mintPassABI,
        CONFIG.MINTPASS_ADDRESS
      );
      const erc721 = new web3.eth.Contract(
        // @ts-expect-error
        erc721ABI,
        CONFIG.ERC721_ADDRESS
      );
      dispatch(
        connectSuccess({
          account: accounts[0],
          minter,
          mintPass,
          erc721,
          web3,
          networkId,
        })
      );
      provider.on("accountsChanged", (accounts) => {
        dispatch(updateAccount(accounts[0]));
      });
      provider.on("chainChanged", async () => {
        const networkId = await web3.eth.net.getId();
        dispatch(
          connectSuccess({
            account: accounts[0],
            minter,
            mintPass,
            erc721,
            web3,
            networkId,
          })
        );
      });
    } catch (err) {
      dispatch(connectFailed("Something went wrong."));
    }
  };
};

export const sign = () => {
  return async (dispatch) => {
    // await web3Modal.clearCachedProvider();
    const provider = await web3Modal.connect();
    // dispatch(connectRequest());
    let web3 = new Web3(provider);
    try {
      const accounts = await web3.eth.getAccounts();
      const signature = await web3.eth.personal.sign(
        "Rawrr",
        accounts[0],
        null
      );
      await fetch(`${CONFIG.API_URL}/raffle`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          address: accounts[0],
          signature: signature,
        }),
      });
      const response = await fetch(`${CONFIG.API_URL}/raffle/${accounts[0]}`, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      const { status } = await response.json();
      const hasParticipated = status === "participated";
      dispatch(
        updateRaffleStatus({ hasParticipatedInRaffle: hasParticipated })
      );
    } catch (err) {
      console.error(err);
    }
  };
};

export const updateAccount = (account) => {
  return async (dispatch) => {
    dispatch(updateAccountRequest({ account: account }));
    dispatch(fetchData());
  };
};
